m: translateX(-50%);\n    ////background: #776112;\n    //z-index: 10;\n  }\n\n  .desk {\n    position: absolute;\n    left: 0px;\n    top: 140px;\n    right: 0px;\n    bottom: 0px;\n    display: flex;\n    z-index: 15;\n\n    .menu {\n      width: 260px;\n      text-align: center;\n      padding-top: 25px;\n      overflow: visible;\n      //overflow-y: auto;\n\n      .button {\n        border: 1px solid green;\n        -webkit-backdrop-filter: blur(10px);\n        backdrop-filter: blur(10px);\n        transform: skewX(-10deg);\n        height: 50px;\n        width: 180px;\n        margin: 10px;\n        border-radius: 20px 5px 20px 0px;\n        position: relative;\n        display: inline-flex;\n        align-items: center;\n        justify-content: center;\n        transition: all 0.3s ease;\n        font: 14px sans-serif;\n        font-weight: 300;\n        text-shadow: 0 0 20px #fff;\n        text-transform: uppercase;\n        //-webkit-animation: breath2 2s 0.5s infinite alternate;\n        //animation: breath2 2s 0.5s infinite alternate;\n        cursor: pointer;\n      }\n\n      .button:before {\n        content: \"\";\n        display: block;\n        width: calc(100% - 22px);\n        height: calc(50px - 8px);\n        //-webkit-animation: breath 2s infinite alternate;\n        //animation: breath 2s infinite alternate;\n        left: 10px;\n        top: 3px;\n        position: absolute;\n        background-color: transparent;\n        border: 1px solid #fff;\n        border-radius: 15px 3px 15px 3px;\n      }\n\n      .button.fire {\n        border-color: #ffeca8;\n        background-image: linear-gradient(to bottom, rgba(255, 138, 48, 0.6), rgba(240, 96, 29, 0.6));\n        box-shadow: 0 0 70px rgba(255, 138, 48, 0.6), 0 5px 20px rgba(255, 138, 48, 0.6), inset 0 1px #ffeca8, inset 0 -1px #ffeca8;\n        color: #2b281d;\n      }\n\n      .button.fire:before {\n        box-shadow: inset 0 0 30px 0 #ffeca8;\n      }\n\n      .button.ice {\n        border-color: #a8ecff;\n        background-image: linear-gradient(to bottom, rgba(48, 138, 255, 0.5), rgba(29, 96, 240, 0.5));\n        box-shadow: 0 0 70px rgba(48, 138, 255, 0.5), 0 5px 20px rgba(48, 138, 255, 0.5), inset 0 1px #ffeca8, inset 0 -1px #ffeca8;\n        color: #12191a;\n      }\n\n      .button.ice:before {\n        box-shadow: inset 0 0 30px 0 #a8ecff;\n      }\n\n      .button:hover.fire {\n        box-shadow: 0 0 70px rgba(255, 138, 48, 0.8), 0 5px 20px rgba(255, 138, 48, 0.8), inset 0 1px #ffeca8, inset 0 -1px #ffeca8;\n      }\n\n      .button:hover.fire:before {\n        box-shadow: inset 0 0 50px 0 #ffeca8;\n      }\n\n      .button:hover.ice {\n        box-shadow: 0 0 70px rgba(48, 138, 255, 0.8), 0 5px 20px rgba(48, 138, 255, 0.8), inset 0 1px #a8ecff, inset 0 -1px #a8ecff;\n      }\n\n      .button:hover.ice:before {\n        box-shadow: inset 0 0 50px 0 #a8ecff;\n      }\n\n      .button + .button {\n        margin-top: 15px;\n        -webkit-animation-delay: 0.3s;\n        animation-delay: 0.3s;\n      }\n\n      .ref {\n        -webkit-backdrop-filter: blur(10px);\n        backdrop-filter: blur(10px);\n        border: 1px solid rgba(255, 255, 255, 0.6);\n        border-radius: 3px;\n        padding: 5px 8px;\n        position: absolute;\n        font-size: 16px;\n        bottom: 10px;\n        right: 10px;\n        color: #fff;\n        font-weight: 300;\n        font-family: sans-serif;\n        text-decoration: none;\n      }\n\n      .ref::first-letter {\n        font-size: 12px;\n      }\n    }\n\n\n    .demo {\n      flex-grow: 2;\n      display: flex;\n      flex-direction: column;\n\n      iframe {\n        flex-grow: 2;\n        border: none;\n        background: transparent !important;\n        backface-visibility: hidden;\n        transform: translate3d(0px, 0px, 0px);\n      }\n\n      .details {\n        min-height: 50px;\n        display: flex;\n        background: #bfbfbf;\n        overflow: hidden;\n        border-radius: 5px;\n        margin: 3px;\n\n        .description {\n          @include use_hvCenteredContent();\n          flex-grow: 3;\n          padding: 5px;\n          overflow: auto;\n\n          * {\n            text-align: center;\n          }\n        }\n\n        .codesandbox {\n          height: 100%;\n          width: 48px;\n          float: right;\n          //display: inline-block;\n          @include use_hvCenteredContent();\n\n          img {\n            height: 48px;\n          }\n        }\n      }\n    }\n  }\n\n\n  @media (max-width: 700px) {\n    flex-direction: column-reverse;\n    .header {\n      //  width: 80%;\n      //  left: 50%;\n      //  top: 0vw;\n      //  transform: translateX(-50%);\n      //  font-size: 14px;\n      //  text-align: center;\n      //  background: #ffffff88;\n      //  //border-radius: 15px;\n      //  z-index: 20;\n      //  color: #2b281d;\n      //  padding-left: 0;\n\n      *, a {\n        color: #2b281d;\n        margin: 0;\n      }\n    }\n    .RadialSelect {\n      //width: 95%;\n      //top: -10px;\n      ////z-index: 1000;\n      ////width: 550px;\n      ////height: 263px;\n      //left: 50%;\n      //height: 43vw;\n      //transform: translateX(-50%) perspective(150px) rotateX(27deg);\n\n      svg {\n        //transform: perspective(100px) rotateX(-10deg);\n      }\n    }\n    .menu {\n      display: none;\n    }\n    .desk {\n      top: 38vw;\n\n      .demo {\n        height: 100%;\n        width: 100%;\n\n        iframe {\n          @include use_hvCenteredAbs();\n          transform: translate(-50%, -50%) scale(.7);\n          width: 143%;\n          height: 143%;\n        }\n\n      }\n\n      .details {\n        position: absolute;\n        bottom: 0;\n        left: 0;\n        width: 100%;\n      }\n    }\n  }\n}\n\n@-webkit-keyframes breath {\n  0% {\n    transform: scaleX(1);\n  }\n  100% {\n    transform: scaleX(0.95);\n  }\n}\n\n@keyframes breath {\n  0% {\n    transform: scaleX(1);\n  }\n  100% {\n    transform: scaleX(0.95);\n  }\n}\n\n@-webkit-keyframes breath2 {\n  0% {\n    transform: skewX(-10deg) scaleX(1);\n  }\n  100% {\n    transform: skewX(-10deg) scaleX(0.95);\n  }\n}\n\n@keyframes breath2 {\n  0% {\n    transform: skewX(-10deg) scaleX(1);\n  }\n  100% {\n    transform: skewX(-10deg) scaleX(0.95);\n  }\n}","/*!\n *   The MIT License (MIT)\n *   Copyright (c) 2020. Nathanael Braun\n *\n *   Permission is hereby granted, free of charge, to any person obtaining a copy\n *   of this software and associated documentation files (the \"Software\"), to deal\n *   in the Software without restriction, including without limitation the rights\n *   to use, copy, modify, merge, publish, distribute, sublicense, and/or sell\n *   copies of the Software, and to permit persons to whom the Software is\n *   furnished to do so, subject to the following conditions:\n *\n *   The above copyright notice and this permission notice shall be included in all\n *   copies or substantial portions of the Software.\n *\n *   THE SOFTWARE IS PROVIDED \"AS IS\", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR\n *   IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF MERCHANTABILITY,\n *   FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. IN NO EVENT SHALL THE\n *   AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER\n *   LIABILITY, WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, ARISING FROM,\n *   OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE\n *   SOFTWARE.\n *\n *   @author : Nathanael Braun\n *   @contact : n8tz.js@gmail.com\n */\n\n@mixin use_hvCenteredContent() {\n\n  &:before {\n    content: ' ';\n    display: inline-block;\n    height: 100%;\n    width: 1px;\n    margin-right: -1px;\n    vertical-align: middle;\n    overflow: hidden;\n  }\n  text-align: center;\n  > * {\n    text-align: left;\n    vertical-align: middle;\n    display: inline-block;\n  }\n}\n\n@mixin use_hvCenteredAbs() {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n}\n","/*!\n *   The MIT License (MIT)\n *   Copyright (c) 2020. Nathanael Braun\n *\n *   Permission is hereby granted, free of charge, to any person obtaining a copy\n *   of this software and associated documentation files (the \"Software\"), to deal\n *   in the Software without restriction, including without limitation the rights\n *   to use, copy, modify, merge, publish, distribute, sublicense, and/or sell\n *   copies of the Software, and to permit persons to whom the Software is\n *   furnished to do so, subject to the following conditions:\n *\n *   The above copyright notice and this permission notice shall be included in all\n *   copies or substantial portions of the Software.\n *\n *   THE SOFTWARE IS PROVIDED \"AS IS\", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR\n *   IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF MERCHANTABILITY,\n *   FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. IN NO EVENT SHALL THE\n *   AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER\n *   LIABILITY, WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, ARISING FROM,\n *   OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE\n *   SOFTWARE.\n *\n *   @author : Nathanael Braun\n *   @contact : n8tz.js@gmail.com\n */\n\n.github-corner:hover .octo-arm {\n  animation: octocat-wave 560ms ease-in-out\n}\n\n@keyframes octocat-wave {\n  0%, 100% {\n    transform: rotate(0)\n  }\n  20%, 60% {\n    transform: rotate(-25deg)\n  }\n  40%, 80% {\n    transform: rotate(10deg)\n  }\n}\n\n@media(max-width: 500px) {\n  .github-corner:hover .octo-arm {\n    animation: none\n  }\n  .github-corner .octo-arm {\n    animation: octocat-wave 560ms ease-in-out\n  }\n}","\n.RadialSelect {\n  user-select: none;\n\n  .radialBtn {\n    font-family: 'Aladin', cursive;\n  }\n\n  .items {\n    width: 100%;\n    height: 100%;\n\n  }\n}"],"sourceRoot":""}]);
